import DataLoadBaseJob from "./data-load-base.job";

class GetJiraSearchIssuesJob extends DataLoadBaseJob {

  constructor(widgetId, cronTime, jqls) {

    var options = {
      service: "get-jira-search-issues-job",
      data: {
        cronTime,
        jqls
      }
    };

    super(widgetId, options);
  }
}

export default GetJiraSearchIssuesJob;