import DataLoadBaseJob from "./data-load-base.job";

class JenkinsLastBuildsJob extends DataLoadBaseJob {
  constructor(widgetId, cronTime, jenkinsName, jobNames) {

    var options = {
      service: "get-last-jenkins-builds-job",
      data: {
        cronTime,
        jenkinsName,
        jobNames,
      }
    };
    super(widgetId, options);
  }
}

export default JenkinsLastBuildsJob;